import Letter from '../components/letter';

class Page extends React.Component {
	constructor(props) {
    super(props);
    this.key = 0;
    this.state = {
    	audioIsAllowed: true
    };
  }

  toggleAudio = (event) => {
  	console.log(event.target.checked);
  	this.setState({
	  	audioIsAllowed: event.target.checked
	  });
  }

	render() {
		// Rerender the Letter component to refire the animation on each keystroke.
		// I know, yuk.
		++this.key;
		return (
			<div>
		  	<Letter value="â€¢" key={this.key} />
		  	<div className="audio-control">
					<input 
						id="toggle-audio"
						type="checkbox"
						checked={this.state.audioIsAllowed}
						onChange={this.toggleAudio}  />
			  	<label htmlFor="toggle-audio">Disable audio</label>
			  </div>
			  <style jsx>{`
					.audio-control {
						position: absolute;
					}
				`}</style>
		  </div>
		)
	}
}

export default Page
